import React from 'react'
import {NavLink} from 'react-router-dom'
import './index.css'

const Nevbar =()=>{
    return(
        <>
        <div className="container-fluid nev-bg" >
            <div className="row">
                <div className="col-10 mx-auto" >
           
                <nav className="navbar navbar-expand-lg navbar-light">
  <NavLink className="navbar-brand" id="icon" to="/">ChauhanTech</NavLink>
  <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
    <span className="navbar-toggler-icon"></span>
  </button>
  <div className="collapse navbar-collapse" id="navbarSupportedContent">
    <ul className="navbar-nav ml-auto">
      <li className="nav-item active">
        <NavLink
        activeClassName="manu_active"  //activeClass say pata chal jayega ki kon sa link active hi
        exact                 //iss ka bina hamesh home activeClass active rahega        
        className="nav-link" to="/">Home <span className="sr-only">(current)</span></NavLink>
      </li>
      <li className="nav-item">
        <NavLink activeClassName="manu_active" className="nav-link" to="/Service">Services</NavLink>
      </li>
      <li className="nav-item">
        <NavLink activeClassName="manu_active" className="nav-link" to="about">About</NavLink>
      </li>
      <li className="nav-item">
        <NavLink activeClassName="manu_active" className="nav-link" to="contact">Contact</NavLink>
      </li>
    </ul>
    
  </div>
</nav>
                
                </div>
            </div>
        </div>
        </>
    )
}
export default Nevbar